<template>
  <button
    class="btn"
    style=" width: 90px;height: 200px"
    data-bs-toggle="tooltip"
  >
    <img
      style="max-width: 100%;height: auto;"
      :src="
        require(`@/assets/diablo2/images/item/${itemCategory.category}.png`)
      "
    />
    <br />
    {{ itemCategory.categoryKo }}
  </button>
</template>

<script>
import itemCategoryList from "@/assets/diablo2/data/itemCategory.json";

export default {
  props: { category: String },
  data() {
    return {
      itemCategoryList,
    };
  },
  setup(props) {
    // itemCategory set
    var targetItemCategory = null;
    for (var i = 0; i < itemCategoryList.length; i++) {
      if (itemCategoryList[i].category === props.category) {
        targetItemCategory = itemCategoryList[i];
        break;
      }
    }

    return { itemCategory: targetItemCategory };
  },
  methods: {},
};
</script>

<style></style>
